}
button.toast-close-button{padding:0;cursor:pointer;background:transparent;border:0;-webkit-appearance:none
}
.toast-icon{position:absolute;left:10px;top:25px
}
.toast-icon,.toast-icon img{width:30px;height:30px
}
.toast-top-center{top:0;right:0;width:100%
}
.toast-bottom-center{bottom:0;right:0;width:100%
}
.toast-top-full-width{top:0;right:0;width:100%
}
.toast-bottom-full-width{bottom:0;right:0;width:100%
}
.toast-top-left{top:12px;left:12px
}
.toast-top-right{top:12px;right:12px
}
.toast-bottom-right{right:12px;bottom:12px
}
.toast-bottom-left{bottom:12px;left:12px
}
.cxlt-toastr-container{position:fixed;z-index:999999;pointer-events:none
}
.cxlt-toastr-container *{-webkit-box-sizing:border-box;box-sizing:border-box
}
.cxlt-toastr-container>div{position:relative;pointer-events:auto;overflow:hidden;margin:0 0 6px;padding:15px 15px 15px 50px;width:300px;border-radius:3px 3px 3px 3px;background-position:15px;background-repeat:no-repeat;-webkit-box-shadow:0 0 12px #999;box-shadow:0 0 12px #999;color:#fff;opacity:.8;-ms-filter:progid:DXImageTransform.Microsoft.Alpha(Opacity=80);filter:alpha(opacity=80)
}
.cxlt-toastr-container>:hover{-webkit-box-shadow:0 0 12px #000;box-shadow:0 0 12px #000;opacity:1;-ms-filter:progid:DXImageTransform.Microsoft.Alpha(Opacity=100);filter:alpha(opacity=100);cursor:pointer
}
.cxlt-toastr-container.toast-bottom-center>div,.cxlt-toastr-container.toast-top-center>div{width:300px;margin-left:auto;margin-right:auto
}
.cxlt-toastr-container.toast-bottom-full-width>div,.cxlt-toastr-container.toast-top-full-width>div{width:96%;margin-left:auto;margin-right:auto
}
.toast{background-color:#030303
}
.toast-success{background-color:#51a351
}
.toast-error{background-color:#bd362f
}
.toast-info{background-color:#2f96b4
}
.toast-warning{background-color:#f89406
}
.toast-progress{position:absolute;left:0;bottom:0;height:4px;background-color:#000;opacity:.4;-ms-filter:progid:DXImageTransform.Microsoft.Alpha(Opacity=40);filter:alpha(opacity=40)
}
@media (max-width:240px){
.cxlt-toastr-container>div{padding:8px 8px 8px 50px;width:11em
}
.cxlt-toastr-container .toast-close-button{right:-.2em;top:-.2em
}
}
@media (min-width:241px) and (max-width:480px){
.cxlt-toastr-container>div{padding:8px 8px 8px 50px;width:18em
}
.cxlt-toastr-container .toast-close-button{right:-.2em;top:-.2em
}
}
@media (min-width:481px) and (max-width:768px){
.cxlt-toastr-container>div{padding:15px 15px 15px 50px;width:25em
}
}
